
.news {
  padding-top: 60px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
}
.news .nav {
  height: 80%;
  width: 200px;
  text-align: center;
  border-right: 5px solid #b44847;
}
.news .nav li {
  height: 45px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  cursor: pointer;
  font-size: 20px;
}
.news .nav .active {
  color: #fff;
  background: #ed4040;
  border-radius: 4px;
  margin: 0 20px;
}
.news .main {
  padding: 12px;
}
.news .main .item {
  width: 300px;
  border: 1px solid #eee;
}
.news .main .item img {
  height: 150px;
}
.news .main .item .see {
  font-size: 16px;
  color: #008000;
  margin-right: 10px;
}
.news .main .item h3 {
  color: #008000;
  font-size: 20px;
  margin: 10px;
}
.news .main .item p {
  line-height: 1.4;
  margin: 10px;
}


